import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";

// Define proper Job type interface
interface Job {
  id: string;
  title: string;
  description: string;
  company: string;
  // Add other job properties you expect
}

interface JobState {
  jobs: Job[];
  currentJob: Job | null;
  status: "idle" | "loading" | "succeeded" | "failed";
  error: string | null;
}

const initialState: JobState = {
  jobs: [],
  currentJob: null,
  status: "idle",
  error: null,
};

const API_URL = "https://akil-backend.onrender.com/opportunities/search";

export const fetchJobs = createAsyncThunk(
  "jobs/fetchJobs",
  async (_, { rejectWithValue }) => {
    try {
      const response = await axios.get(API_URL);

      // Handle different possible response structures
      const jobsData =
        response.data?.data || // If nested under 'data'
        response.data?.jobs || // If nested under 'jobs'
        response.data?.results || // If paginated API
        response.data; // Default

      // Ensure we always return an array
      return Array.isArray(jobsData) ? jobsData : [];
    } catch (error: any) {
      return rejectWithValue(
        error.response?.data?.message || "Failed to fetch jobs"
      );
    }
  }
);

export const fetchJobById = createAsyncThunk(
  "jobs/fetchJobById",
  async (id: string, { rejectWithValue }) => {
    try {
      const response = await axios.get(
        `https://akil-backend.onrender.com/opportunities/${id}`
      );
      return response.data;
    } catch (error: any) {
      return rejectWithValue(
        error.response?.data?.message || "Failed to fetch job"
      );
    }
  }
);

const jobsSlice = createSlice({
  name: "jobs",
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(fetchJobs.pending, (state) => {
        state.status = "loading";
        state.error = null;
      })
      .addCase(fetchJobs.fulfilled, (state, action) => {
        state.status = "succeeded";
        state.jobs = action.payload;
      })
      .addCase(fetchJobs.rejected, (state, action) => {
        state.status = "failed";
        state.error = (action.payload as string) || "Failed to fetch jobs";
      })
      .addCase(fetchJobById.pending, (state) => {
        state.status = "loading";
        state.error = null;
      })
      .addCase(fetchJobById.fulfilled, (state, action) => {
        state.status = "succeeded";
        state.currentJob = action.payload;
      })
      .addCase(fetchJobById.rejected, (state, action) => {
        state.status = "failed";
        state.error =
          (action.payload as string) || "Failed to fetch job details";
      });
  },
});

export default jobsSlice.reducer;
